"use client";

import React, { useState, useMemo } from "react";
import Head from 'next/head';
import {
  Calculator, Zap, ShieldCheck, TrendingUp, Search,
  Target, Award, History, BookOpen, ChevronRight, Scale, Layers,
  Info, CheckCircle2, AlertCircle, ArrowUpRight, Sparkles, 
  BarChart3, LineChart, PieChart, Activity, Receipt, IndianRupee
} from 'lucide-react';

// --- Sub-Calculators (Brokerage and Equity specific logic added) ---
import SIPCalculator from "../../Calculators/SubCalculators/SIPCalculator";
import LumpsumCalculator from "../../Calculators/SubCalculators/LumpsumCalculator";
import GoalCalculator from "../../Calculators/SubCalculators/GoalCalculator";
import EMICalculator from "../../Calculators/SubCalculators/EMICalculator";
// Assume BrokerageCalculator is a new component you'll create
// import BrokerageCalculator from "../../Calculators/SubCalculators/BrokerageCalculator";

const contentData = {
  brokerage: {
    title: "Trade Net-Profit Analyzer",
    formula: "Net Profit = (Selling Price - Buying Price) - Total Regulatory Charges",
    variables: [
      { k: "STT", v: "Securities Transaction Tax" },
      { k: "SEBI Fee", v: "Market Regulator Charges" },
      { k: "Stamp Duty", v: "State-wise Transaction Tax" }
    ],
    specs: [
      { feature: "Asset Class", value: "Equity/F&O/Intraday" },
      { feature: "Taxation", value: "GST @18% on Brokerage" },
      { feature: "Break-even", value: "Price needed to cover costs" }
    ],
    logic: [
      { step: "Gross Calculation", desc: "Determining the absolute difference between Buy & Sell value." },
      { step: "Tax Deductions", desc: "Calculating mandatory government taxes like STT and Stamp Duty." },
      { step: "Net Realization", desc: "The final 'In-hand' profit after subtracting all statutory fees." }
    ],
    pros: ["Prevents hidden cost surprises", "Helps in planning exit price"],
    cons: ["Taxes vary by state", "Doesn't include DP charges"],
    tips_tricks: ["For intraday trades, STT is only charged on the sell side, saving you costs."],
    insight: "Profit isn't just Sell - Buy. Regulatory charges can take up to 0.1% of your total turnover."
  },
  sip: {
    title: "SIP Wealth Projector",
    formula: "FV = P √ó ({[1 + i]^n ‚Äì 1} / i) √ó (1 + i)",
    variables: [{ k: "P", v: "Monthly Investment" }, { k: "i", v: "Monthly Interest" }, { k: "n", v: "Tenure" }],
    specs: [{ feature: "Strategy", value: "Rupee Cost Averaging" }, { feature: "Tax", value: "LTCG Applicable" }],
    logic: [{ step: "Power of 10", desc: "Small monthly sums grow through the magic of compounding." }],
    pros: ["Disciplined approach", "Low entry barrier"],
    cons: ["Requires long-term patience"],
    tips_tricks: ["Treat SIP as a 'salary deduction' for maximum discipline."],
    insight: "Time in the market is superior to timing the market."
  }
};

const CalculatorSection = () => {
  const [searchTerm, setSearchTerm] = useState("");
  const [activeCalculator, setActiveCalculator] = useState("brokerage");

  const CALCULATOR_OPTIONS = useMemo(() => [
    // { id: 'brokerage', label: 'Brokerage & Tax', icon: 'üßæ', category: 'Trading', component: BrokerageCalculator },
    { id: 'sip', label: 'SIP Projector', icon: 'üìà', category: 'Investment', component: SIPCalculator },
    { id: 'lumpsum', label: 'Lumpsum Pro', icon: 'üí∞', category: 'Investment', component: LumpsumCalculator },
    { id: 'goal', label: 'Goal Architect', icon: 'üéØ', category: 'Planning', component: GoalCalculator },
    { id: 'emi', label: 'Loan Matrix', icon: 'üí≥', category: 'Banking', component: EMICalculator },
  ], []);

  const filteredOptions = CALCULATOR_OPTIONS.filter(opt => 
    opt.label.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const current = contentData[activeCalculator] || contentData.sip;
  const ActiveComponent = CALCULATOR_OPTIONS.find(opt => opt.id === activeCalculator)?.component;

  return (
    <div className="bg-[#f8fafc] dark:bg-[#020617] min-h-screen transition-all duration-500">
      <Head><title>{current.title} | Financial Intelligence</title></Head>

      <main className="max-w-[1400px] mx-auto px-4 py-8 grid lg:grid-cols-12 gap-8">
        
        {/* SIDEBAR */}
        <aside className="lg:col-span-3">
          <div className="sticky top-24 space-y-6">
            <div className="relative group">
              <Search className="absolute left-4 top-1/2 -translate-y-1/2 text-slate-400 group-focus-within:text-blue-500" size={18} />
              <input 
                type="text" placeholder="Search tools..."
                className="w-full bg-white dark:bg-slate-900 border-2 border-slate-100 dark:border-slate-800 rounded-2xl py-4 pl-12 pr-4 text-xs font-black focus:border-blue-500 outline-none shadow-sm"
                onChange={(e) => setSearchTerm(e.target.value)}
              />
            </div>

            <nav className="space-y-2 max-h-[70vh] overflow-y-auto no-scrollbar">
              {filteredOptions.map((tab) => (
                <button
                  key={tab.id}
                  onClick={() => setActiveCalculator(tab.id)}
                  className={`flex items-center gap-4 px-5 py-4 rounded-2xl font-black text-[11px] uppercase tracking-widest transition-all w-full border-2 ${
                    activeCalculator === tab.id ? "bg-blue-600 text-white border-blue-600 shadow-lg translate-x-1" : "bg-white dark:bg-slate-900 text-slate-500 border-transparent hover:border-slate-100 dark:hover:border-slate-800"
                  }`}
                >
                  <span className="text-xl">{tab.icon}</span>
                  <div className="text-left">
                    <p className="leading-none mb-1">{tab.label}</p>
                    <span className="text-[8px] opacity-60 font-bold">{tab.category}</span>
                  </div>
                </button>
              ))}
            </nav>
          </div>
        </aside>

        {/* MAIN TERMINAL */}
        <div className="lg:col-span-9 space-y-8">
          
          <header className="bg-slate-900 rounded-[3rem] p-8 md:p-12 text-white relative overflow-hidden">
            <div className="relative z-10 flex flex-col md:flex-row justify-between md:items-center gap-6">
              <div className="space-y-2">
                <span className="text-blue-400 text-[10px] font-black uppercase tracking-[0.4em] flex items-center gap-2">
                  <Activity size={14} /> Trade Intelligence
                </span>
                <h1 className="text-4xl md:text-6xl font-black italic uppercase leading-tight tracking-tighter">
                  {current.title}
                </h1>
              </div>
              <div className="flex flex-col gap-2">
                <div className="bg-white/10 px-6 py-3 rounded-2xl border border-white/20 backdrop-blur-md">
                   <p className="text-[10px] font-black opacity-50 uppercase mb-1">Live Tax Engine</p>
                   <p className="text-emerald-400 font-black text-sm flex items-center gap-2">
                      <IndianRupee size={14} /> UPDATED 2025
                   </p>
                </div>
              </div>
            </div>
            <div className="absolute -right-20 -top-20 w-80 h-80 bg-blue-600/10 rounded-full blur-[100px]" />
          </header>

          {/* CALCULATOR INTERFACE */}
          <section className="bg-white dark:bg-slate-900 rounded-[3rem] p-6 md:p-10 shadow-2xl border border-slate-100 dark:border-slate-800">
             {ActiveComponent && <ActiveComponent />}
          </section>

          {/* DYNAMIC MASTERING SECTION */}
          <div className="bg-gradient-to-br from-blue-600 to-indigo-700 rounded-[3rem] p-10 text-white shadow-xl relative overflow-hidden">
             <div className="grid md:grid-cols-2 gap-10 items-center relative z-10">
                <div className="space-y-6">
                   <h3 className="text-3xl font-black uppercase italic tracking-tighter flex items-center gap-3">
                     <Receipt size={32} /> Breaking Down the Math
                   </h3>
                   <p className="text-blue-100 text-sm leading-relaxed font-medium">
                      {current.insight}
                   </p>
                   <div className="grid grid-cols-2 gap-4">
                      <div className="bg-white/10 p-4 rounded-2xl border border-white/10">
                         <p className="text-[10px] font-black opacity-60 uppercase mb-2 text-blue-200">Processing Mode</p>
                         <p className="text-xs font-bold">Post-Tax Analysis</p>
                      </div>
                      <div className="bg-white/10 p-4 rounded-2xl border border-white/10">
                         <p className="text-[10px] font-black opacity-60 uppercase mb-2 text-blue-200">Regulatory Sync</p>
                         <p className="text-xs font-bold">SEBI/GST Compliant</p>
                      </div>
                   </div>
                </div>
                <div className="bg-white/10 backdrop-blur-xl rounded-[2.5rem] p-8 border border-white/20 text-center relative">
                   <p className="text-[10px] font-black uppercase tracking-widest text-blue-200 mb-6">Execution Logic Visualization</p>
                   
                   {/* Conditional Visuals */}
                   <div className="flex flex-col items-center">
                      {activeCalculator === 'brokerage' ? (
                        <>
                          <div className="flex gap-2 items-end h-24 mb-4">
                             <div className="w-8 bg-blue-400 rounded-t-lg h-full animate-bounce" style={{animationDelay: '0.1s'}} />
                             <div className="w-8 bg-blue-200 rounded-t-lg h-3/4 animate-bounce" style={{animationDelay: '0.2s'}} />
                             <div className="w-8 bg-emerald-400 rounded-t-lg h-1/2 animate-bounce" style={{animationDelay: '0.3s'}} />
                          </div>
                          <p className="text-[10px] font-bold text-blue-100">Charge Decomposition Chart</p>
                        </>
                      ) : (
                        <TrendingUp size={64} className="text-blue-300 animate-pulse" />
                      )}
                   </div>
                </div>
             </div>
          </div>

          

          {/* TECH DATA GRID */}
          <div className="grid md:grid-cols-2 gap-8">
            {/* Tech Specs */}
            <div className="bg-white dark:bg-slate-900 rounded-[2.5rem] border border-slate-100 dark:border-slate-800 shadow-xl overflow-hidden">
              <div className="bg-slate-50 dark:bg-slate-800/50 px-8 py-6 border-b dark:border-slate-800">
                <h3 className="text-[10px] font-black uppercase tracking-widest text-slate-500 flex items-center gap-2">
                  <Scale size={14} /> Regulatory Specifications
                </h3>
              </div>
              <div className="divide-y dark:divide-slate-800">
                {current.specs?.map((spec, idx) => (
                  <div key={idx} className="flex justify-between items-center px-8 py-5 hover:bg-blue-50/50 dark:hover:bg-blue-900/10 transition-colors">
                    <span className="text-[11px] font-black uppercase text-slate-400">{spec.feature}</span>
                    <span className="text-xs font-black text-slate-900 dark:text-white uppercase tracking-tight">{spec.value}</span>
                  </div>
                ))}
              </div>
            </div>

            {/* Formula Block */}
            <div className="bg-slate-950 rounded-[2.5rem] p-8 text-white border border-slate-800 shadow-2xl relative overflow-hidden group">
              <History className="absolute -right-10 -bottom-10 text-white/5 w-40 h-40 group-hover:rotate-12 transition-transform" />
              <h3 className="text-[10px] font-black uppercase tracking-widest text-blue-500 mb-8 flex items-center gap-2">
                <Zap size={14} className="fill-current" /> Core Wealth Algorithm
              </h3>
              <div className="bg-white/5 p-6 rounded-3xl border border-white/10 text-center mb-8">
                <code className="text-lg md:text-xl font-mono font-bold text-blue-400 tracking-tighter">
                  {current.formula}
                </code>
              </div>
              <div className="space-y-4">
                {current.variables?.map((v, i) => (
                  <div key={i} className="flex justify-between text-[10px] font-bold border-b border-white/5 pb-2">
                    <span className="text-blue-500">{v.k}</span>
                    <span className="text-slate-400 uppercase tracking-widest text-right">{v.v}</span>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/* STRATEGIC INSIGHTS GRID */}
          <div className="grid md:grid-cols-3 gap-6">
            <div className="bg-emerald-50 dark:bg-emerald-900/10 p-8 rounded-[2rem] border border-emerald-100 dark:border-emerald-900/20">
              <h3 className="font-black text-[10px] uppercase mb-6 text-emerald-600 flex items-center gap-2">
                 <CheckCircle2 size={14}/> Trade Benefits
              </h3>
              <ul className="space-y-4">
                {current.pros?.map((p, i) => (
                  <li key={i} className="text-xs font-bold text-slate-700 dark:text-slate-300 flex items-start gap-2">
                    <span className="text-emerald-500 mt-0.5">‚Ä¢</span> {p}
                  </li>
                ))}
              </ul>
            </div>
            
            <div className="bg-rose-50 dark:bg-rose-900/10 p-8 rounded-[2rem] border border-rose-100 dark:border-rose-900/20">
              <h3 className="font-black text-[10px] uppercase mb-6 text-rose-600 flex items-center gap-2">
                 <AlertCircle size={14}/> Important Risks
              </h3>
              <ul className="space-y-4">
                {current.cons?.map((c, i) => (
                  <li key={i} className="text-xs font-bold text-slate-700 dark:text-slate-300 flex items-start gap-2">
                    <span className="text-rose-500 mt-0.5">‚Ä¢</span> {c}
                  </li>
                ))}
              </ul>
            </div>

            <div className="bg-slate-900 p-8 rounded-[2rem] text-white flex flex-col justify-between shadow-2xl">
              <div>
                <h3 className="font-black text-[10px] uppercase mb-6 text-blue-400 flex items-center gap-2">
                   <BookOpen size={14}/> Pro Tip
                </h3>
                <p className="text-sm font-bold leading-relaxed italic opacity-90 tracking-tight">
                  "{current.tips_tricks?.[0]}"
                </p>
              </div>
              <button className="mt-8 group flex items-center gap-2 text-[10px] font-black uppercase text-blue-400 hover:text-white transition-all">
                Learn Net Realization <ArrowUpRight size={16} className="group-hover:translate-x-1 group-hover:-translate-y-1 transition-transform" />
              </button>
            </div>
          </div>

        </div>
      </main>

      <style jsx global>{`
        .no-scrollbar::-webkit-scrollbar { display: none; }
        .no-scrollbar { -ms-overflow-style: none; scrollbar-width: none; }
        ::selection { background: #2563eb; color: white; }
      `}</style>
    </div>
  );
};

export default CalculatorSection;